body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
	/* background-color: wheat; */
}

* {
	box-sizing: border-box;
}

.grid {
	display: grid;
	grid-template-columns: 1fr 1fr;
}

.intro {
	background-image: url("https://res.cloudinary.com/dusr8fbuo/image/upload/v1607197647/scott-webb-KpuWtiszFQw-unsplash_mqa1qx.jpg");
	background-size: cover;
	background-repeat: no-repeat;
	height: 100vh;
	width: 100%;
}

.content-links {
	grid-column: 2/3;
	background-color: rgb(240, 239, 219);
	height: 100vh;
	display: grid;
	width: 100%;
}

.greeting {
	margin-top: 60px;
	font-size: 150px;
	letter-spacing: -13px;
	background: -webkit-linear-gradient(rgb(197, 232, 183), rgb(66, 52, 62));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	font-family: "Shrikhand", cursive;
	text-align: center;
}

.statement {
	font-family: "Cabin", sans-serif;
	font-size: 27px;
	text-align: justify;
	text-align: center;
	padding: 0px 20px 20px 20px;
	line-height: 150%;
	text-align: center;
	letter-spacing: 1px;
}

.skills-list {
	font-family: "Cabin", sans-serif;
	font-size: 22px;
	text-align: justify;
	text-align: center;
	color: #fff;
	padding: 0px 20px 20px 20px;
	line-height: 150%;
	text-align: center;
	letter-spacing: 3px;
}

.title {
	margin-top: 60px;
	font-size: 65px;
	letter-spacing: -6px;
	color: rgb(98, 87, 85);
	font-family: "Shrikhand", cursive;
	text-align: center;
}

.hover-content {
	width: 100%;
	padding: 0 100px;
	font-family: "Cabin", sans-serif;
	font-size: 18px;
	justify-content: center;
	text-align: center;
	transition: 0.9s ease;
	opacity: 0;
}

#about {
	font-size: 16px;
}
.section {
	transition: 0.1s;
	-webkit-tap-highlight-color: transparent;
}
.section:hover {
	/* background-color: rgba(125, 134, 117, 0.1); */
	background-color: rgb(244, 240, 238);
	border: 3px solid black;
	border-style: double;
}

.section:hover .title {
	transition: all 0.5s;
	background: -webkit-linear-gradient(rgb(98, 87, 85), rgb(244, 202, 145));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}
.section:hover .hover-content {
	opacity: 1;
}

.social-icons {
	display: flex;
	justify-content: center;
}

.social-icons i {
	/* padding: 10px; */
	padding-left: 10px;
	padding-right: 10px;
	transition: all 0.2s ease-in-out;
	margin-top: 10px;
}

.social-icons i:hover {
	transform: rotateY(180deg);
}

.social-icons a {
	color: black;
	text-decoration: none; /* no underline */
}

.email-btn {
	display: none;
}

.brand {
	font-family: "Cabin", sans-serif;
	font-size: 35px;
	background: -webkit-linear-gradient(rgb(98, 87, 85), rgb(244, 202, 145));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}
.ml-auto {
	font-family: "Cabin", sans-serif;
	font-size: 20px;
	color: black;
	/* background: -webkit-linear-gradient(rgb(98, 87, 85), rgb(244, 202, 145));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent; */
}

.nav-link {
	letter-spacing: -2px;
}
.nav-link:hover {
	text-decoration: underline;
}

.footer {
	position: fixed;
	right: 0;
	bottom: 0;
	left: 0;
	color: black;
	background-color: transparent;
	height: 44px;
	text-align: center;
	display: flex;
	flex-direction: column;
}

