body {
	font-family: "Raleway", sans-serif;
	background-color: rgb(240, 239, 219);
}

.page-title {
	font-family: "Shrikhand", cursive;
	text-align: center;
	font-size: 85px;
	letter-spacing: -7px;
	margin-top: 2%;
	background: -webkit-linear-gradient(rgb(197, 232, 183), rgb(66, 52, 62));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	overflow: hidden; /* Ensures the content is not revealed until the animation */
	border-right: 0.15em solid orange; /* The typwriter cursor */
	white-space: nowrap; /* Keeps the content on a single line */
	margin: 0 auto; /* Gives that scrolling effect as the typing happens */
	animation: typing 7s steps(30, end), blink-caret 2s step-end infinite;
}
.proj-container {
	margin-top: 7%;
	position: absolute;
	top: 10%;
	left: 40%;
	width: 300px;
	height: 500px;
	perspective: 900px;
}
.proj-card {
	position: absolute;
	top: 0%;
	width: 100%;
	height: 100%;
	transition: 0.5s all ease;
	transform: rotateX(60deg) scale(0.7);
	perspective: 900px;
	box-shadow: 0px 10px 30px rgb(244, 240, 238);
	animation: entry 4s linear 1;
}

.proj-card:hover {
	cursor: pointer;
	transform: rotate(0deg) scale(1) translateY(10px);
	transition: 0.3s all ease;
	z-index: 400;
}

#c0 {
	position: absolute;
	top: 0%;
	width: 100%;
	height: 100%;
	z-index: 300;
	box-shadow: 0px 20px 100px rgb(255, 255, 255);
}

#c1 {
	box-shadow: 0px 20px 100px rgb(255, 255, 255);
	left: 100%;
	z-index: 0;
}

#c2 {
	left: -100%;
	z-index: 0;
	box-shadow: 0px 20px 100px rgb(255, 255, 255);
}

.image {
	position: absolute;
	text-align: center;
	top: 0%;
	left: 0%;
	width: 100%;
	height: 45%;
	/* background-color: white; */
	/* background: linear-gradient(to top, rgb(98, 87, 85) 30%, #d99267 100%); */
}

img {
	margin: 50px;
}

.proj-info {
	position: absolute;
	top: 50%;
	left: 0%;
	height: 55%;
	width: 100%;
	/* background: linear-gradient(to bottom, #eba65b 0%, #d99267 40%); */
	z-index: 30;
	color: black;
	font-family: "Cabin", sans-serif;
	size: 35px;
}

.proj-titl {
	text-align: center;
	color: rgb(248, 205, 150);
	text-shadow: 10px 10px 10px #eee;
	letter-spacing: 2px;
}

.proj-desc {
	font-size: 14px;
	/* color: #fff; */
	color: black;
	padding: 20px;
	line-height: 150%;
	text-align: center;
	letter-spacing: 1px;
}

.proj-btns {
	display: flex;
	justify-content: center;
}

.proj-btns i {
	padding: 40px;
	transition: all 0.2s;
}

.proj-btns i:hover {
	padding: 40px;
	transform: scale(1.3);
}

@keyframes simple {
	50% {
		transform: translateX(10px);
		opacity: 0.5;
	}
	100% {
		transform: translateX(10px);
		opacity: 0;
	}
}

@keyframes simple1 {
	50% {
		transform: translateX(-10px);
		opacity: 0.5;
	}
	100% {
		transform: translateX(-10px);
		opacity: 0;
	}
}

@keyframes entry {
	0% {
		top: -20%;
		opacity: 0.1;
	}
	100% {
		top: 0%;
	}
}

/* these first keyframes are for the typewritter effect */
@keyframes typing {
	from {
		width: 0;
	}
	to {
		width: 100%;
	}
}

/* The typewriter cursor effect */
@keyframes blink-caret {
	from,
	to {
		border-color: transparent;
	}
	50% {
		border-color: orange;
	}
}

